//コンテンツタイトル
.contentTitle{
	margin-bottom: 5px;
	color: #828282;
	line-height: 1.3;
	position: relative;
	&__main{
		font-size: 18px;
		letter-spacing: 0.05em;
		font-weight: bold;
	}
	&__caption{
		font-weight: bold;
		font-size: 0.85rem;
		margin-top: 3px;
	}
	&__button{
		position: absolute;
		top: 0;
		right: 0;
		a{
			border: 1px solid #666;
			padding: 4px 10px;
			line-height: 1;
			color: inherit;
			font-size: 11px;
			display: inline-block;
		}
	}
	@media all and (min-width: $breakpoint + 1){
		display: flex;
		align-items: center;
		&__main{
			font-size: 24px;
		}
		&__caption{
			margin: 0 0 0 10px;
		}
		&__button{
			a{
				padding: 5px 15px;
				font-size: 0.9rem;
				@include hover;
			}
		}
	}
}

.button{
	border-radius: 5px;
	display: inline-block;
	color: #fff;
	position: relative;
	background-color: #53a7db;
	border-bottom: 4px solid #3d80aa;
	padding: 8px 15px;

	&__inner{
		font-size: 1.1rem;
		// font-weight: bold;
		line-height: 1.2;
		display: inline-block;
		text-shadow: 1px 1px 2px rgba(0,0,0,0.5);
		&--arrow{
			background: url('../../images/arrow.svg') left center/18px auto no-repeat;
			padding-left: 25px;
		}
	}

	&--coursor{
		background-image: url('../../images/coursor.png');
		background-position: right bottom;
		background-size: auto 90%;
		background-repeat: no-repeat;
		padding: 10px 20px;
		.button__inner{
			font-size: 1.2rem;
			font-weight: bold;
		}
	}
	&--entry{
		padding: 5px 0;
		background-color: #00bb33;
		border-bottom: 4px solid #009629;
		width: 100%;
		text-align: center;
		.button__inner{
			padding: 5px 20px;
			text-align: center;
			font-size: 0.9rem;
			position: relative;
			&::after{
				content: '';
				position: absolute;
				width: 100%;
				height: 100%;
				top: 0;
				left: 0;
				background: url('../../images/mail.svg') right center/auto 90% no-repeat;
				opacity: 0.4;
			}
			em{
				font-size: 1.8rem;
				color: #fff;
				display: block;
				margin-bottom: 3px;
			}
		}
	}
	@media all and (min-width: $breakpoint + 1){
		padding: 12px 40px;
		@include hover;
		&__inner{
			font-size: 20px;
			&--arrow{
				background: url('../../images/arrow.svg') left center/24px auto no-repeat;
				padding-left: 35px;
			}
		}
	}
}

.viewMore{
	// margin-top: 20px;
	text-align: center;
	&__button{
		padding: 10px 20px;
		background-color: #e8e8e8;
		line-height: 1;
		font-weight: bold;
		color: #666;
		display: inline-block;
		&--large{
			font-size: 1.15rem;
			width: 100%;
		}
	}
	@media all and (min-width: $breakpoint + 1){
		// margin-top: 30px;
		&__button{
			padding: 10px 20px;
			font-size: 1.15rem;
			@include hover;
			&--large{
				padding: 15px 20px;
				font-size: 1.25rem;
				max-width: 600px;
			}
		}
	}
}

.selectContainer{
	display: inline-block;
	position: relative;

	@media all and (max-width: $breakpoint){
		&::after{
			content: '';
			position: absolute;
			width: 0;
			height: 0;
			top: 50%;
			right: 10px;
			border: 5px solid transparent;
			border-top: 5px solid #666;
			transform: translateY(-2px);
		}
		.categorySelect{
			appearance: none;
			border-radius: 2px;
			border: 1px solid #666;
			padding: 3px 25px 3px 5px;
		}
	}
}


/*---------------------------------
SIDE
---------------------------------*/
.companyInfo{
	font-size: 0.85rem;
	border-spacing: 0;
	th{
		border-right: 1px solid #ccc;
		border-bottom: 1px solid #ccc;
		width: 50px;
		text-align: left;
		padding: 5px 10px 5px 2px;
	}
	td{
		border-bottom: 1px solid #ccc;
		padding: 5px 5px 5px 10px;
	}
	tr{
		&:last-child{
			th,td{
				border-bottom: none;
			}
		}
	}
}

.qaData{
	background-color: #fff;
	display: flex;
	// justify-content: space-between;
	font-size: 11px;
	&__item{
		width: 50%;
		padding: 10px;
		display: flex;
		align-items: flex-end;
		border: 1px solid #ccc;
		border-top: none;
		&:nth-child(even){
			border-left: none;
		}
	}
	&__itemHeadline{
		.qIcon{
			margin-right: 5px;
			font-weight: bold;
		}
		.Icon{
			margin-right: 5px;
			width: 18px;
			height: 18px;
			background-color: #00bb33;
			border-radius: 50%;
			color: #fff;
			line-height: 18px;
			text-align: center;
		}
	}
	&__itemValue{
		margin-left: auto;
		text-align: right;
		em{
			font-size: 14px;
			line-height: 1;
		}
	}
	@media all and (min-width: $breakpoint + 1){
		display: block;
		font-size: 1rem;
		padding: 10px;
		&__item{
			width: 100%;
			padding-bottom: 10px;
			margin-bottom: 10px;
			border: none;
			border-bottom: 2px dotted #ccc;
		}
		&__itemHeadline{
			.Icon{
				font-size: 0.9rem;
			}
		}
		&__itemValue{
			em{
				font-size: 1.4rem;
				margin-right: 3px;
			}
		}
	}
}

.searchForm{
	margin-top: 15px;
	&__inner{
		padding: 15px;
	}
	&__text{
		font-size: 0.9rem;
		margin-bottom: 3px;
	}
	&__form{
		display: flex;
		justify-content: space-between;
		align-items: flex-start;
		input,button{
			appearance: none;
			border: none;
			border-radius: 3px;
			font-size: 1rem;
		}
		input{
			padding: 5px;
			border-width: 1px;
			border-style: solid;
			width: calc(100% - 90px);
		}
		button{
			padding: 5px;
			width: 70px;
			background-color: #666;
			color: #fff;
			font-weight: bold;
			letter-spacing: 0.1em;
			text-shadow: 1px 1px 2px rgba(0,0,0,0.2);
			box-shadow: 1px 1px 0 1px rgba(0,0,0,0.4);
		}
	}
	@media all and (min-width: $breakpoint + 1){
		margin: 0;
		padding: 10px;
		background-color: #fff;
		&__inner{

		}
		&__form{
			button{
				cursor: pointer;
				@include hover;
			}
		}
	}
}

.postQuestion{
	background-color: #fff;
	border-width: 4px;
	border-style: solid;
	padding: 10px;
	text-align: center;
	display: flex;
	align-items: center;

	&__text{
		font-size: 9px;
		margin-bottom: 8px;
	}
	@media all and (max-width: $breakpoint){
		.button{
			margin-left: auto;
			padding: 10px 13px;
			&__inner{
				font-size: 1rem;

			}
		}
	}
	@media all and (min-width: $breakpoint + 1){
		border-width: 5px;
		padding: 15px;
		text-align: center;
		display: block;
		&__text{
			font-size: 0.85rem;
			margin-bottom: 10px;
			line-height: 1.3;
		}
	}
}

.tagCloud{
	&__tag{
		display: inline-block;
		margin-right: 3px;
		margin-bottom: 6px;
		a{
			display: block;
			padding: 5px;
			line-height: 1;
			border-radius: 3px;
			border: 1px solid #999;
			font-size: 0.8rem;
			color: $textColor;
		}
	}
	@media all and (min-width: $breakpoint + 1){
		&__tag{
			margin-right: 5px;
			margin-bottom: 10px;
			a{
				padding: 10px;
				font-size: 0.85rem;
				@include hover;
			}
		}
	}
}

/*---------------------------------
FOOTER
---------------------------------*/
.globalFooter{
  background-color: #fff;
  position: relative;
	padding-bottom: $sp_footerHight;
	a{
		color: inherit;
	}
  &__inner{
    padding: 10px;
  }

  &__nav{
    display: flex;
		align-items: center;
		justify-content: center;
  }
  &__navItem{
    font-size:  8px;
		line-height: 1.2;
    &:not(:last-child){
      margin-right: 5px;
			padding-right: 5px;
			border-right: 1px solid #ccc;
    }
  }
	&__copyright{
		margin-top: 8px;
    width: 100%;
    text-align: center;
    font-size: 8px;
    color: #999;
  }

  @media all and (min-width: $breakpoint + 1){
		padding-bottom: 0;
    &__inner{
      display: flex;
      justify-content: space-between;
      padding: 10px;
			max-width: $contentWidth;
			margin: 0 auto;
    }

    &__nav{
    }

    &__navItem{
			&:not(:last-child){
				margin-right: 15px;
				padding-right: 15px;
			}
      font-size: 0.8rem;
			a{
        @include hover;
      }
    }
		&__copyright{
			width: 250px;
			text-align: right;
			font-size: 0.8rem;
    }
  }
}

.spFooter{
	position: fixed;
	z-index: 999;
	height: $sp_footerHight;
	display: flex;
	align-items: center;
	left: 0;
	bottom: 0;
	width: 100%;
	box-shadow: 0 -2px 3px rgba(0,0,0,0.2);
	background-color: #fff;
	// border-top: 1px solid #ccc;
}
